import { useState } from "react";
import "./styles.css";
import { Button } from "./ui/button";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from "./ui/dropdown-menu";

import {
  CurrencyCircleDollar,
  Fingerprint,
  HandGrabbing,
  Headset,
  ShoppingCart,
  SpinnerBall,
  Users,
  Wallet,
} from "@phosphor-icons/react";
import { Grip } from "lucide-react";
const apps = [
  {
    name: "My Account",
    icon: Users,
    url: "https://myaccount.dreamemirates.com",
    url2: "https://staging.myaccount.dreamemirates.com",
  },
  {
    name: "Physical Trading",
    icon: CurrencyCircleDollar,
    url: "https://physicaltrading.dreamemirates.com",
    url2: "https://staging.physicaltrading.dreamemirates.com",
  },
  {
    name: "Virtual Trading",
    icon: ShoppingCart,
    url: "https://virtualtrading.dreamemirates.com ",
    url2: "https://staging.virtualtrading.dreamemirates.com",
  },
  {
    name: "Identity",
    icon: Fingerprint,
    url: "https://identity.dreamemirates.com",
    url2: "https://staging.identity.dreamemirates.com",
  },

  {
    name: "Wallet",
    icon: Wallet,
    url: "https://wallet.dreamemirates.com",
    url2: "https://staging.wallet.dreamemirates.com",
  },
  {
    name: "Meta Dream",
    icon: HandGrabbing,
    url: "https://metadream.dreamemirates.com",
    url2: "https://staging.metadream.dreamemirates.com",
  },
  {
    name: "Big Draw",
    icon: SpinnerBall,
    url: "https://bigdraw.dreamemirates.com",
    url2: "https://staging.bigdraw.dreamemirates.com",
  },
  {
    name: "Support",
    icon: Headset,
    url: "https://support.dreamemirates.com",
    url2: "https://staging.support.dreamemirates.com",
  },
];
export const AllApps = ({
  isProduction = false,
}: {
  isProduction?: boolean;
}) => {
  const [isOpen, setIsOpen] = useState(false);
  return (
    <DropdownMenu open={isOpen} onOpenChange={setIsOpen}>
      <DropdownMenuTrigger asChild>
        <Button
          variant="outline"
          size="icon"
          className={`rounded-xl cursor-pointer h-10 w-10 border ${
            isOpen ? "bg-gray-400" : ""
          }`}
        >
          <Grip />
        </Button>
      </DropdownMenuTrigger>
      <DropdownMenuContent className="w-80 p-4" align="end" sideOffset={8}>
        <div className="grid grid-cols-3 gap-4">
          {apps.map(({ name, icon: Icon, url, url2 }) => (
            <DropdownMenuItem key={name} asChild>
              <a
                href={isProduction ? url : url2}
                className="flex flex-col h-20 items-center justify-center gap-2 p-3 cursor-pointer focus:bg-zinc-100 rounded-xl border no-underline text-center"
                target="_blank"
                rel="noopener noreferrer"
              >
                <Icon className="w-5 h-5" weight="duotone" size={23} />
                <span className="text-center text-xs">{name}</span>
              </a>
            </DropdownMenuItem>
          ))}
        </div>
      </DropdownMenuContent>
    </DropdownMenu>
  );
};
